.barContainer {
    position: relative;
    display: flex;
    justify-content: space-between;
}

.barLine {
    position: absolute;

}

.barUnitContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.barUnitText {

}

.barUnitRound {
    background-color: #EAEEF1;
    width: 2rem;
    height: 2rem;
    border-radius: 100%;
}

.barUnitRound.complete {
    background-color: red;
}